import "./index.less";
import _$ from 'jquery';

new Promise((resolve) => {
    _$(function () {
        if (!_$('#snow')[0]) {
            _$('body').append(_$(`<div class="snow_ct"><canvas id="snow"></canvas></div>`));
        }
        resolve(true);
    })
})
.then(() => {
    // requestAnimationFrame 60

    let _RequestAnimationFrame = function (callback) {
        window.setTimeout(callback, 1000 / 60);
    },
    requestAnimationFrame = window.requestAnimationFrame ||
                            window.mozRequestAnimationFrame ||
                            window.webkitRequestAnimationFrame ||
                            window.msRequestAnimationFrame ||
                            _RequestAnimationFrame;

    window.requestAnimationFrame = requestAnimationFrame;
})
.then(() => {
    const screenWidth = screen.width;
    const screenHeight = screen.height;
    const snowCanvas = document.getElementById('snow');
    const ctx = snowCanvas.getContext("2d");

    snowCanvas.width = window.innerWidth;
    snowCanvas.height = window.innerHeight;

    // 
    let mX = -200,
        mY = -200,
        snowNums = 200,
        arr = [];

    // Canvas
    function resetCanvas () {
        ctx.clearRect(0, 0, snowCanvas.width, snowCanvas.height);
    }

    // 
    function oneFrameCanvas () {
        resetCanvas();
        arr.forEach((item) => {
            item.move();
        });

        // 
        window.requestAnimationFrame(oneFrameCanvas);
    }

    // 
    class snow {
        constructor () {
            this.initSnow();
            this.move();
        }

        initSnow () {
            this.props = {
                x: Math.random() * snowCanvas.width,
                y: Math.random() * snowCanvas.height,
                size: (Math.random() * 3) + 2,
                speed: (Math.random() * 1) + 0.5,
                velX: 0, // 0
                opacity: (Math.random() * 0.5) + 0.3,
                stepSize: (Math.random()) / 30 * 1,
                step: 0
            };
            
            this.props.velY = this.props.speed;
        }

        isOutOfCanvas () {
            let flake = this.props;
            
            // canvas
            return (flake.y >= snowCanvas.height || flake.y <= 0) || (flake.x >= snowCanvas.width || flake.x <= 0);
        }

        move () {
            let flake = this.props,
                x = mX,
                y = mY,

                // 
                minDist = 150,

                x2 = flake.x,
                y2 = flake.y,
                
                dist = Math.sqrt((x2 - x) * (x2 - x) + (y2 - y) * (y2 - y)), // 
                dx = x2 - x, // 
                dy = y2 - y; // 

            
            // 
            if (dist < minDist) {

                // 
                let force = minDist / (dist * dist),
                    xcomp = (x - x2) / dist,
                    ycomp = (y - y2) / dist,
                    deltaV = force / 2;
                flake.velX -= deltaV * xcomp;
                flake.velY -= deltaV * ycomp;
            } else {
                // 
                // 
                flake.velX *= 0.98;
                
                
                // if (flake.velY < 0) {

                //     // 
                //     flake.velY *= 0.99999999;
                //     if (flake.velY < -0.000001) {
                //         console.log(flake.velY)
                //         flake.velY = 0.0001;
                //     }
                // } else if (flake.velY < flake.speed && flake.velY > 0.0) {

                //     // 
                //     flake.velY *= 1.02;
                // } else if (flake.velY > flake.speed) {

                //     // 
                //     flake.velY = flake.speed
                // }

                // COS
                flake.velX += Math.cos(flake.step += .05) * flake.stepSize;
            }

            ctx.fillStyle = `rgba(255, 255, 255, ${flake.opacity})`;

            flake.y += flake.velY;
            flake.x += flake.velX;

            if (this.isOutOfCanvas()) {
                this.reset();
            }

            ctx.beginPath();
            ctx.arc(flake.x, flake.y, flake.size, 0, Math.PI * 2);
            ctx.fill();
        }

        reset () {
            this.initSnow();
            this.props.y = 0;
        }

    }

    // 
    document.addEventListener("mousemove", function (e) {
        mX = e.clientX,
        mY = e.clientY
    });

    // canvas
    window.addEventListener("resize", function () {
        snowCanvas.width = window.innerWidth;
        snowCanvas.height = window.innerHeight;
    });
    

    // 
    for(let i = 0; i < snowNums; i++) {
        arr.push(new snow());
    }

    // 
    oneFrameCanvas();

});
